<template>
  <div>
    <div class="search">
      <div class="container-fluid">
        <div class="row">
          <div class="col-xs-12 col-sm-6 col-lg-4 media">
            <h3>
              <button id="advance-search" class="btn btn-white" @click="showForm = !showForm">Advanced Property Search: Type, Price, Acres...</button>
            </h3>
            <hr>
          </div>
        </div>
      </div>
    </div>

    <div class="row" v-if="showForm">

      <div class="sec1">
        <!-- start sec1-->
        <div class="form-group">
          <div class="container-fluid">
            <div class="row">

              <div class="col-xs-3">
                <div>
                  <label class="ptTitle pt">
                    <b>Property Types:</b>
                  </label>
                </div>



                <div class="form-check">
                  <label class="checkbox-inline pt">Lakefront
                  <input type="checkbox" name="exampleRadios" id="exampleRadios1" value="option1" @click="checkboxProperties" checked>
                  </label>
                </div>
                <div class="form-check">
                  <label class="checkbox-inline pt">Riverfront
                  <input type="checkbox" name="exampleRadios" id="exampleRadios1" value="option2" @click="checkboxProperties">
                  </label>
                </div>
                <div class="form-check">
                  <label class="checkbox-inline pt">Islands</label>
                  <input type="checkbox" name="exampleRadios" id="exampleRadios1" value="option3" @click="checkboxProperties">
                </div>
                <div class="form-check">
                  <label class="checkbox-inline pt">Cottages/Cabins</label>
                  <input type="checkbox" name="exampleRadios" id="exampleRadios1" value="option4" @click="checkboxProperties">
                </div>
                <div class="form-check">
                  <label class="checkbox-inline pt">Remote Access</label>
                  <input type="checkbox" name="exampleRadios" id="exampleRadios1" value="option5" @click="checkboxProperties">
                </div>
                <div class="form-check">
                  <label class="checkbox-inline pt">Developement/Resorts</label>
                  <input type="checkbox" name="exampleRadios" id="exampleRadios1" value="option6" @click="checkboxProperties">
                </div>
                <div class="form-check">
                  <label class="checkbox-inline pt">Hunting Land/Lrg Acreage</label>
                  <input type="checkbox" name="exampleRadios" id="exampleRadios1" value="option7" @click="checkboxProperties">
                </div>
                <div class="form-check">
                  <label class="checkbox-inline pt">Unorganized Township</label>
                  <input type="checkbox" name="exampleRadios" id="exampleRadios1" value="option8" @click="checkboxProperties">
                </div>

              </div>

              <div class="col-xs-6">
                <div>
                  <label class="ptTitle pa">
                    <b>Property Acreage:</b>
                  </label>
                </div>
                <div>
                  <label class="checkbox-inline pa"></label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties(1, 50)">1 - 50
                </div>
                <div>
                  <label class="checkbox-inline pa"></label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties(51, 100)">51 - 100
                </div>
                <div>
                  <label class="checkbox-inline pa"></label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties(101, 150)">101 - 150
                </div>
                <div>
                  <label class="checkbox-inline pa"></label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties(151, 200)">151 - 200
                </div>
                <div>
                  <label class="checkbox-inline pa"></label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties(201, 300)">201 - 300
                </div>
                <div>
                  <label class="checkbox-inline pa"></label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties(301, 400)">301 - 400
                </div>
                <div>
                  <label class="checkbox-inline pa"></label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties(401, 500)">401 - 500
                </div>
                <div>
                  <label class="checkbox-inline pa"></label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties(501)">501 +
                </div>
              </div>

              <div class="col-xs-9">
                <div>
                  <label class="ptTitle sp">
                    <b>Sale Price:</b>
                  </label>
                </div>
                <div>
                  <label class="checkbox-inline sp"></label>
                  <input type="checkbox" id="inlineCheckbox1" data-size="large" data-toggle="toggle" @click="checkboxProperties(1, 50000)"> 0.00-50K
                </div>
                <div>
                  <label class="checkbox-inline sp"></label>
                  <input type="checkbox" id="inlineCheckbox1" data-size="large" data-toggle="toggle" @click="checkboxProperties(51000, 100000)"> 51-100K
                </div>
                <div>
                  <label class="checkbox-inline sp"></label>
                  <input type="checkbox" id="inlineCheckbox1" data-size="large" data-toggle="toggle" @click="checkboxProperties(101000, 200000)"> 101-200K
                </div>
                <div>
                  <label class="checkbox-inline sp"></label>
                  <input type="checkbox" id="inlineCheckbox1" data-size="large" data-toggle="toggle" @click="checkboxProperties(201000, 300000)"> 201-300K
                </div>
                <div>
                  <label class="checkbox-inline sp"></label>
                  <input type="checkbox" id="inlineCheckbox2" data-size="large" data-toggle="toggle" @click="checkboxProperties(301000, 400000)"> 301-400K
                </div>
                <div>
                  <label class="checkbox-inline sp"></label>
                  <input type="checkbox" id="inlineCheckbox1" data-size="large" data-toggle="toggle" @click="checkboxProperties(401000, 600000)"> 401-600K
                </div>
                <div>
                  <label class="checkbox-inline sp"></label>
                  <input type="checkbox" id="inlineCheckbox2" data-size="large" data-toggle="toggle" @click="checkboxProperties(601000, 800000)"> 601-800K
                </div>
                <div>
                  <label class="checkbox-inline sp"></label>
                  <input type="checkbox" id="inlineCheckbox3" data-size="large" data-toggle="toggle" @click="checkboxProperties(801000)"> 801k+
                </div>
              </div>

              <div class="col-xs-12">
                <div>
                  <label class="ptTitle ammen">
                    <b>Ammenities:</b>
                  </label>
                </div>
                <div>
                  <label class="checkbox-inline ammen">Waterline</label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties">
                </div>
                <div>
                  <label class="checkbox-inline ammen">Powerline</label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties">
                </div>
                <div>
                  <label class="checkbox-inline ammen">Body of Water</label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties">
                </div>
                <div>
                  <label class="checkbox-inline ammen">Access Road</label>
                  <input type="checkbox" id="inlineCheckbox" data-size="large" data-toggle="toggle" @click="checkboxProperties">
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!--end sec1-->

      <!--Search Properties small button-->

      <div class="text-center col-md-12">
        <div class="row">
          <button class="btn btn-primary ss-btn" type="Submit">
            <b>Start Search</b>
          </button>
        </div>
      </div>

      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'search',
  data() {
    return {
      showForm: false,
      min: 0,
      max: 0,
      query: {},
      propertyModel: {
      }
    }
  },
  // mounted() {
  //   this.$store.dispatch('searchProperties', this.query)
  // },

  computed: {
    properties() {
      return this.$store.state.properties
    }
  },
  methods: {
    searchProperties() {
      for (var prop in this.propertyModel) {
        if (this.propertyModel[prop].value) {
          // { $lt: this.propertyModel[prop].value }
          if (prop == 'salePrice' || prop == 'totalAcreage') {
            this.query[prop] = { "$gte": this.min, "$lt": this.max }
          } else {
            this.query[prop] = this.propertyModel[prop].value
          }
        }
      }
      this.$store.dispatch('searchProperties', this.query)
      this.query = {}
      this.min = 0
      this.max = 0
    },
    checkboxProperties(minValue, maxValue) {
      this.propertyModel.salePrice.value = true
      this.min = minValue
      this.max = maxValue
      console.log(this.min)
    }

  },
  components: {}
}

</script>


<style scoped>

.btn-white {
  transition: all .5s ease;
  background: #d8d8d8;
  color: #000;
  font-size: 1.5rem;
  font-weight: 600;
  border-radius: 10px;
  height: 45px;
  margin-left: 73%;
}

.btn-white:hover {
  background: #efefef;
  transform: scale(1.2);
}

.form-group {
  font-size: 20px;
  margin-left: 40px;
}

.ptTitle {
  font-size: 24px;
  color: black;
  padding-top: 25px;
  padding-right: 100px;
}

.pt {
  padding-left: 50px;
}

.pa {
  padding-left: 110px;
}

.sp {
  padding-left: 30px;
}

.ammen {
  padding-left: 70px;
}

.ss-btn {
  font-size: 22px;
  background: #d8d8d8;
  color: black;
  padding: 10px 10px;
  padding-bottom: 10px;
  margin-left: 800px;
  margin-bottom: 20px;
  border-radius: 10px;
}
</style>










